import React from "react";
// import { Button } from "../../components/Button";
import banner from "../../assets/img/banner.jpg";
import discord from "../../assets/img/discord.svg";
import twitter from "../../assets/img/twitter.svg";
import "./Hero.scss";

export const Hero = () => {
  return (
    <div className="welcome-container" id="hero">
      <div className="links-wrapper">
        <div className="links-container">
          <h1 className="welcome-title">Ape Control To Major Tom</h1>
          <h3 className="welcome-description">Drop date: Aug, 13 @ 10PM UTC // 6PM EST // 11PM BST</h3>
          <div className="links" id="minting">
            <a
              className="icon-disc"
              href={process.env.REACT_APP_DISCORD_URL}
              target="_blank"
              rel="noreferrer"
            >
              <div className="icon-wrapper">
                <img src={discord} alt="discord" />
              </div>
            </a>
            <a
              className="icon-twi"
              href={process.env.REACT_APP_TWITTER_URL}
              target="_blank"
              rel="noreferrer"
            >
              <div className="icon-wrapper">
                <img src={twitter} alt="twitter" />
              </div>
            </a>
          </div>
          {/* <div className="button-wrap">
            <Button label="Mint Apes" link="/mint" />
          </div> */}
        </div>
      </div>
      <img src={banner} alt="banner" className="banner" />
    </div>
  );
};
