import {
  Redirect,
  Route,
  BrowserRouter as Router,
  Switch,
  useLocation,
} from "react-router-dom";
import { Hero } from "./views/Hero/Hero";
import { About } from "./views/About/About";
import { Footer } from "./views/Footer/Footer";
import Header from "./views/Header/Header";
import Roadmap from "./views/Roadmap/Roadmap";
import { Mint } from "./views/Mint";
import banner from "./assets/img/banner.gif"
import "./assets/fonts/sharpie.css";
import "helvatica-neue-lt/index.css";
import "./App.scss";

const App = () => {
  return (
    <Router>
      <Switch>
        <Route
          exact
          path="/"
          render={() => (
            <div style={{ overflowX: "hidden" }}>
              <Header />
              <Hero />
              <Mint />
              <About />
              {/*<div className="d-flex center banner">*/}
              {/*  <a href="https://bit.ly/2ZJsZRo" target="_blank" rel="noreferrer">*/}
              {/*    <img className="" src={banner} alt="banner" />*/}
              {/*  </a>*/}
              {/*</div>*/}
              <Roadmap />
              {/* <button onClick={handleConnect}>
              {account ? `${account}` : `Connect metamask`}
            </button>
            <div>
              <button onClick={() => handleBuy("1")}>Buy 1 card</button>
            </div> */}
              <Footer />
            </div>
          )}
        />
        <Route path="/mint" component={Mint} />
        <Redirect to="/" />
      </Switch>
    </Router>
  );
};

export default App;
