import React from "react";
import ape1 from "../../assets/img/ape1.jpg";
// import ape2 from "../../assets/img/ape2.jpg";
import apes from "../../assets/img/apes.gif";
import ape3 from "../../assets/img/ape3.jpg";
import "./About.scss";

export const About = () => {
  return (
    <div className="about-container" id="about">
      <div className="about-block d-grid">
        <div className="d-flex center">
          <img className="img marginTop" src={ape1} alt="ape" />
        </div>
        <div className="about-text">
          <h2 className="about-text-headline">
            WELCOME TO THE APES OF SPACE WORLD
          </h2>
          <p className="about-text-description">
            For centuries, chimps dominated the jungle, thriving in the lush
            green tropical rainforest. Seeds, fruits, honey, and insects were in
            abundance, and there was little to nothing to worry about. Eat,
            sleep, and have some fun. This was the routine.
          </p>
          <p className="about-text-description">
            However, things began to change when man found the jungle and
            discovered how to make fire. One tree soon turned to two, three, and
            now the once green jungle is almost a desert.
          </p>
          <p className="about-text-description">
            Spartacus who was a simple chimp is now forced to scavenge for food
            and hide from humans. He grew tired of watching people cut their
            jungles, build fires, and steal their bananas.
          </p>
          <p className="about-text-description">
            As fate would have it, he saw a falling star (or so he thought) late
            at night and decided that a new home lies in the stars. Along with
            10000 of his friends and relatives, Spartacus went looking for this
            new home. While they journeyed, a random SpaceX rocket fell nearby.
            They went to repair it. And so, the Apes of Space journey begins.
          </p>
        </div>
      </div>
      <div className="about-block d-grid" id="traits">
        <div className="about-text">
          <h2 className="about-text-headline">TRAITS AND SPECIES</h2>
          <p className="about-text-description">
            Besides the rocket parts, there was a bunch of other junk in the
            jungle, so the monkeys gathered different pieces of clothes,
            randomly put them on, and made selfies in some nice locations. The
            luckiest (and rarest) ones managed to get matching pieces and the
            others got what they could. We have a total of 120 different
            variants of equipment and backgrounds in this collection so each
            monkey is a sight o see!
          </p>
        </div>
        <div className="d-flex center">
          <img className="img" src={apes} alt="ape" />
        </div>
      </div>
      <div className="about-block d-grid" id="community">
        <div className="d-flex center">
          <img className="img marginTop" src={ape3} alt="ape1" />
        </div>
        <div className="about-text">
          <h2 className="about-text-headline">APE IN A BOX</h2>
          <p className="about-text-description">
            We don’t know which monkey gets what clothes, it will be revealed
            only when a monkey gets into the rocket (when you mint a card). But
            we know for sure that some of them are truly unique, like Spartacus,
            Albert, Miss Able, Enos, Gordo, Goliath, Rafiki, Betsy, Pishgam,
            Sam, Winston and Scatback. Corresponding card numbers of those
            glorious apes will be announced later.
          </p>
          <p className="about-text-description">
            The price of each Apes of Space NFT card is 0.035 Ether, they are
            distributed on the Ethereum network, and there is a total of 10000
            cards.
          </p>
        </div>
      </div>
      <div className="about-block d-grid" id="traits">
        <div className="about-text">
          <h2 className="about-text-headline">Roadmap 2.0</h2>
          <p className="about-text-description">
            🔹Merch Store Launch and Charity -  October 2021 <br />
            🔹New NFT collection - Introducing Female Apes - December 2021<br />
            🔹Public release of the Apes of Space game whitepaper for The Sandbox - January 2022<br />
            🔹$AoS Token Drop -  February 2022<br />
            🔹Beta Release of the Apes of Space Play-to-Earn game - April 2022<br />
            🔹Starting a family — Introducing Baby Apes! - June 2022<br />
            🔹Final Release of the Apes of Space Play-to-Earn game - August 2022.
          </p>
        </div>
        <div className="d-flex center">
          <img className="img" src={apes} alt="ape" />
        </div>
      </div>
    </div>
  );
};
